//      
const documentGroups = new Map<string, {
    files: Array<{file_id: string, file_name: string}>,
    timer?: NodeJS.Timeout,
    processed: boolean
}>();

//  
function logDebug(message: string, data?: any) {
    console.log(`[${new Date().toISOString()}] ${message}`, data ? JSON.stringify(data, null, 2) : '');
}

const handleSignedDocuments = new Composer<MyContext>();

handleSignedDocuments.on('document', async (ctx) => {
    const message = ctx.message;
    const mediaGroupId = message.media_group_id;
    const document = message.document;

    logDebug('  :', {
        mediaGroupId,
        fileName: document.file_name,
        fileId: document.file_id,
        fileSize: document.file_size,
        registrationId: ctx.scene.session.registrationId
    });

    //   
    const fileName = document.file_name?.toLowerCase();
    if (!fileName?.endsWith('.pdf') && !fileName?.endsWith('.docx')) {
        logDebug('  :', { fileName });
        await ctx.reply(',      PDF  DOCX');
        return;
    }

    if (!mediaGroupId) {
        logDebug('    ');
        await ctx.reply(',     ,    .');
        return;
    }

    //     
    let group = documentGroups.get(mediaGroupId);
    if (!group) {
        logDebug('   :', { mediaGroupId });
        group = {
            files: [],
            processed: false
        };
        documentGroups.set(mediaGroupId, group);
    }

    //    
    group.files.push({
        file_id: document.file_id,
        file_name: document.file_name
    });
    logDebug('   :', {
        mediaGroupId,
        totalFiles: group.files.length,
        currentFile: document.file_name
    });

    //      
    if (group.timer) {
        clearTimeout(group.timer);
    }

    //      
    group.timer = setTimeout(async () => {
        logDebug('   :', {
            mediaGroupId,
            filesCount: group.files.length
        });

        if (group.processed) {
            logDebug('   :', { mediaGroupId });
            return;
        }

        try {
            group.processed = true;

            //   registrationId
            const registrationId = ctx.scene.session.registrationId;
            if (!registrationId) {
                throw new Error(' registrationId  ');
            }

            logDebug('  :', {
                mediaGroupId,
                registrationId,
                filesCount: group.files.length
            });

            //     
            const uploadPromises = group.files.map(async (doc) => {
                logDebug('     Telegram:', {
                    fileId: doc.file_id,
                    fileName: doc.file_name
                });

                const file = await ctx.telegram.getFile(doc.file_id);
                const fileUrl = `https://api.telegram.org/file/bot${process.env.TELEGRAM_BOT_TOKEN_MASTER}/${file.file_path}`;

                logDebug(' URL :', {
                    fileName: doc.file_name,
                    fileUrl: fileUrl
                });

                return {
                    url: fileUrl,
                    name: doc.file_name
                };
            });

            const uploadedFiles = await Promise.all(uploadPromises);
            logDebug('    :', {
                filesCount: uploadedFiles.length,
                files: uploadedFiles.map(f => f.name)
            });

            //    API
            logDebug('   API:', {
                registrationId,
                filesCount: uploadedFiles.length
            });

            const response = await laravelService.uploadSignedDocuments(
                registrationId,
                uploadedFiles
            );

            logDebug('   API:', { response });

            await ctx.reply('!   .           .');
            
            //  
            documentGroups.delete(mediaGroupId);
            logDebug('     :', { mediaGroupId });
            
            return ctx.scene.leave();
        } catch (error) {
            logDebug('    :', {
                mediaGroupId,
                error: error.message,
                stack: error.stack
            });

            await ctx.reply('    . ,       .');
            
            //     
            documentGroups.delete(mediaGroupId);
        }
    }, 2000); //     2 
});

// Handle final step
//  handleFinalStep :
const handleFinalStep = async (ctx: MyContext) => {
    await ctx.reply(',        .');
    
    try {
        console.log('Attempting to submit registration with data:', ctx.scene.session.registrationForm);
        
        const registrationResponse = await laravelService.submitRegistration(ctx.scene.session.registrationForm);
        console.log('Registration submitted successfully:', registrationResponse);
        
        const registrationId = registrationResponse.data.id;
        ctx.scene.session.documentUpload = {
            documents: [],
            registrationId: registrationId
        };
        
        if (!registrationId) {
            throw new Error('Registration ID not found in response');
        }

        const zipBuffer = await laravelService.generateContract({
            id: registrationId
        });

        await ctx.replyWithDocument({
            source: zipBuffer,
            filename: `_${registrationResponse.data.contract_number}.zip`
        });

        const instructions = `
,   !!!

1.   
2.   
3.         

  :
-      (   )
-     PDF  DOCX
- ,     
-      

     :
   :
1.   
2.  ""
3.        
4.    
5.  ""

   :
1.   
2.  Ctrl     
3.  ""`;

        await ctx.reply(instructions, { parse_mode: 'HTML' });
        return ctx.wizard.next();

    } catch (error) {
        console.error('Error in handleFinalStep:', error);
        
        let errorMessage = '    . ';
        
        if (error.response?.status === 422) {
            const validationErrors = error.response.data.errors;
            if (validationErrors.email) {
                errorMessage += ' email    . ,   email.';
            } else {
                errorMessage += ',    .';
            }
        } else {
            errorMessage += ',  .';
        }
        
        await ctx.reply(errorMessage);
    }
};


// Create and export the wizard scene
export const registrationWizard = new Scenes.WizardScene<MyContext>(
    'registration_wizard',
    showWelcome,
    checkSelfEmployment,
    handleSelfEmployment,
    handleFullName,
    handleBirthDate,
    handlePassport,
    handleIssuedBy,
    handleIssueDate,
    handleDivisionCode,
    handleAddress,
    handleInn,
    handleAccountNumber,
    handleBankName,
    handleBik,
    handleCorrAccount,
    handleBankInn,
    handleBankKpp,
    handlePhone,
    handleEmail,
    handleMedBook,
    handleMedBookExpiry,
    handleEducationCert,
    handleEducationCertPhoto,
    handleSignedDocuments
);