import { Scenes, Markup } from 'telegraf';
import { MyContext } from '../../../types/MyContext';
import { cabinetGate } from "../../../utils/cabinetGate";
import laravelService from "../../../../services/laravelService";
import { changeDescriptionScene } from './changeDescriptionScene';

export const mainScene = new Scenes.BaseScene<MyContext>('main');

mainScene.enter(async (ctx: MyContext) => {
    const messageText = `[   ]`;

    const mainMenuKeyboard = Markup.inlineKeyboard([
        [
            Markup.button.callback('', 'education'),
        ],
        [
            Markup.button.callback(' ', 'documents'),
            Markup.button.callback('  ', 'clients_management'),
        ],
        [
            Markup.button.callback(' ', 'change_description'),
        ],
        [
            Markup.button.callback(' ', 'change_photo'),
            Markup.button.callback('  ', 'change_schedule'),
        ],
        [
            Markup.button.callback('   ', 'logout')  //   
        ]
    ]);

    if (ctx.callbackQuery?.message) {
        try {
            await ctx.editMessageText(messageText, mainMenuKeyboard);
        }
        catch (error) {
            await ctx.reply(messageText, mainMenuKeyboard);
        }
    } else {
        await ctx.reply(messageText, mainMenuKeyboard);
    }
});

//  
mainScene.action('logout', async (ctx: MyContext) => {
    try {
        await ctx.answerCbQuery('  ...');
        
        //   
        const confirmKeyboard = Markup.inlineKeyboard([
            [
                Markup.button.callback(' , ', 'confirm_logout'),
                Markup.button.callback(' ', 'cancel_logout')
            ]
        ]);

        await ctx.editMessageText(
            ' ,     ?',
            confirmKeyboard
        );
    } catch (error) {
        console.error('  :', error);
        await ctx.reply(' .  .');
    }
});

//  
mainScene.action('confirm_logout', async (ctx: MyContext) => {
    try {
        await ctx.answerCbQuery();
        
        //    
        const telegramId = ctx.from?.id;
        if (telegramId) {
            try {
                await laravelService.logout(telegramId);
            } catch (error) {
                //  ,    
                console.error('     :', error);
            }
        }

        //        
        await ctx.editMessageText(
            '    .',
            Markup.inlineKeyboard([
                [Markup.button.callback(' ', 'start_login')]
            ])
        );

        //    
        
        return ctx.scene.enter('login_wizard');

    } catch (error) {
        console.error('  :', error);
        await ctx.reply(' .      .');
        
        //         
        return ctx.scene.enter('login_wizard');
    }
});

//  
mainScene.action('cancel_logout', async (ctx: MyContext) => {
    try {
        await ctx.answerCbQuery('');
        return ctx.scene.reenter(); //    
    } catch (error) {
        console.error('   :', error);
        await ctx.reply(' .  .');
    }
});

